<template>
  <v-container class="fill-height">
    <v-row justify="center" align="center">
      <v-col cols="12" sm="8" md="6">
        <v-card class="pa-6" elevation="8">
          <v-card-title class="text-h5 mb-4">
            Dołącz do sesji
          </v-card-title>
          
          <v-card-text>
            <v-form @submit.prevent="joinSession" ref="form">
              <v-text-field
                v-model="deputyName"
                label="Imię i nazwisko posła"
                :rules="[v => !!v || 'Wymagane pole']"
                required
              ></v-text-field>
              
              <v-btn
                color="primary"
                type="submit"
                block
                :loading="loading"
                class="mt-4"
              >
                Wejdź do sejmu
              </v-btn>
            </v-form>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import websocket from '@/services/websocket'

export default {
  name: 'JoinSessionView',
  data() {
    return {
      deputyName: '',
      loading: false
    }
  },
  methods: {
    async joinSession() {
      if (!this.$refs.form.validate()) return;
      
      this.loading = true;
      const sessionId = this.$route.params.sessionId;
      
      await websocket.connect();
      websocket.send({
        type: 'JOIN_SESSION',
        sessionId,
        deputyName: this.deputyName
      });

      localStorage.setItem('deputyName', this.deputyName);
      localStorage.setItem('sessionId', sessionId);
      
      this.$router.push('/politician-panel');
    }
  }
}
</script> 